.footer {
  background-color: $color-tangaroa;
}

.footer__wrapper {
  display: flex;
  justify-content: space-between;
  padding-top: 37px;
  padding-bottom: 32px;

  @include vp-1023 {
    padding-top: 30px;
    padding-bottom: 28px;
  }

  @include vp-767 {
    flex-direction: column;
  }
}

.footer__menu {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  align-items: center;
  list-style-type: none;
  margin: 0;
  padding: 0;

  @include vp-1023 {
    gap: 16px;
  }

  @include vp-767 {
    flex-direction: column;
    align-items: flex-start;
    gap: 8px;
    margin-bottom: 16px;
  }
}

.footer__logo {
  display: block;
  padding-top: 5px;
  padding-right: 5px;

  @include vp-1023 {
    padding-top: 10px;
    padding-right: 0;
  }

  @include vp-767 {
    padding: 9px 2px;
  }

  @mixin hover-focus {
    opacity: 0.8;
  }

  @mixin active {
    opacity: 0.3;
  }
}

.footer__logo-image {
  fill: $color-alice-blue;

  @include vp-1023 {
    width: 38px;
    height: 20px;
  }

  @include vp-767 {
    width: 42px;
    height: 22px;
  }
}

.footer__item {
  padding: 9px 2px;

  @include vp-1023 {
    padding: 9px 5px 9px 2px;
  }

  @include vp-767 {
    padding: 0;
  }
}

.footer__link {
  font-family: $ff-montserrat;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: $color-alice-blue;
  text-align: center;

  @include vp-1023 {
    font-size: 12px;
    line-height: 15px;
  }

  @include hover-focus {
    opacity: 0.8;
  }

  @include active {
    opacity: 0.3;
  }
}

.footer__social-list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 20px;
  list-style-type: none;
  margin: 0;
  padding: 0;
  margin-top: 6px;

  @include vp-1023 {
    margin-top: 5px;
    gap: 19px;
  }

  @include vp-767 {
    gap: 22px;
  }
}

.footer__social-link {
  display: flex;
  padding: 10px 3px;

  @include vp-1023 {
    padding: 10px 1px;
  }

  @include vp-767 {
    padding: 0;
  }

  @include hover-focus {
    opacity: 0.8;
  }

  @include active {
    opacity: 0.3;
  }
}

.footer__social-icon {
  fill: $color-alice-blue;

  @include vp-1023 {
    width: 16.42px;
    height: 16px;
  }
}

.footer__social-icon--ok {
  @include vp-1023 {
    width: 9.64px;
    height: 16px;
  }
}
