.form {
  display: flex;
  flex-direction: column;
  background-color: $color-tangaroa;
  justify-content: flex-start;
  width: 403px;
  height: auto;
  padding: 48px 50px 52px;

  @include vp-1023 {
    margin: 0 auto;
    padding: 42px 50px 52px;
  }

  @include vp-767 {
    width: 100%;
    padding: 36px 30px;
  }

  form {
    display: flex;
    flex-direction: column;
    gap: 19px;
  }

  input[type="text"],
  input[type="tel"],
  input[type="email"] {
    width: 100%;
    padding: 11px 16px;
    margin: 0;
    border: none;
    background-color: $color-default-white;
    font-family: $ff-montserrat;
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    color: transparent;
    text-shadow: 0 0 0 $color-tangaroa;

    @mixin hover-focus {
      opacity: 0.8;
    }
  }

  &::placeholder {
    color: $color-tangaroa-light;
  }

  .form__agreement {
    position: relative;
    font-family: $ff-montserrat;
    font-weight: 400;
    font-size: 10px;
    line-height: 13px;
    color: $color-alice-blue;
    text-align: center;
    padding-left: 24px;
    padding-top: 5px;

    @include vp-767 {
      text-align: left;
      padding: 0 50px 8px 30px;
    }
  }

  input[type="checkbox"] {
    appearance: none;

    &:checked + .form__checkbox-mark::before {
      background-image: url("../img/svg/checkbox-mark.svg");
      background-repeat: no-repeat;
      background-position: center;
    }


    &:hover + .form__checkbox-mark::before,
    &:focus + .form__checkbox-mark::before,
    &:focus-visible + .form__checkbox-mark::before {
      box-shadow: inset 0 0 0 2px $color-prussian-blue-light;
    }
  }

  .form__checkbox-mark::before {
    content: "";
    position: absolute;
    left: 2px;
    top: 2px;
    width: 18px;
    height: 18px;
    background-color: $color-default-white;

    @include vp-767 {
      top: 4px;
    }
  }

  h3 {
    font-family: $ff-montserrat;
    font-weight: 500;
    font-size: 18px;
    line-height: 27px;
    color: $color-alice-blue;
    margin: 0;
    margin-bottom: 26px;

    @include vp-1023 {
      margin-bottom: 28px;
    }

    @include vp-767 {
      line-height: 25px;
      text-align: center;
    }
  }

  .form__button {
    @include vp-767 {
      padding-top: 12px;
      padding-bottom: 12px;
    }
  }

  .input-message {
    font-family: inherit;
    font-size: 10px;
    color: $color-alice-blue;
  }
}
